import React, {Component} from 'react';
import ViewPortfolio from '../viewing/ViewPortfolio';
import {db} from '../auth/firebase';

class Portfolio extends Component {

    constructor(props) {
        super(props);
        this.state = {
            portfolio: []
        };
    }

    componentDidMount() {
        db.collection("Projects")
            .orderBy("priority", "asc")
            .onSnapshot(querySnapshot => {
                let portfolio = [];
                querySnapshot.forEach(function(doc) {
                    let temp = {};
                    // fill temp
                    temp.id = doc.id;
                    temp.title = doc.data().title;
                    temp.imageUrl = doc.data().imageUrl;
                    temp.url = doc.data().url;
                    // add temp to portfolio
                    portfolio.push(temp);
                });
                this.setState({portfolio});
            })
    }

    render() {
        return (
            <div className="portfolio-container box-shadow">
                <h1 className="header white-text medium-text berlin margin-left">PORTFOLIO<span className="orange-text">.</span></h1>
                <div className="scrollable">
                    <ViewPortfolio portfolio={this.state.portfolio}/>
                </div>
            </div>
        );
    }
}

export default Portfolio