import React from 'react';
import Indicator from './Indicator';

const EachSkill = ({skill, show, hideSkill}) => {
    const showSkills = skill.length ? (
        skill.map((s, idx) => {
            return (
                <div key={idx + 1} className="container-wrapper align no-margin no-padding margin-left-2">
                    <span className="header white-text small-text berlin bold">{s.name}</span>
                    <Indicator level={s.level}/>
                </div>
            );
        })
    ) : (
        <span></span>
    )

    if (skill.length && show) {
        return(
            <div className="container-wrapper justify align no-border margin-bottom-2">
                {showSkills}
            </div>
        )
    } else {
        return(
            <div className="container-wrapper justiy align no-border"></div>
        )
    }
}

export default EachSkill