import React from 'react';

const ViewPortfolio = ({portfolio}) => {
    const showPortfolio = portfolio.length ? (
        portfolio.map(p => {
            return (
                <a href={p.url} key={p.id} className="margin-left margin-right margin-bottom">
                    <span style={{display: "block"}}>
                        <div className="portfolio-container-2 box-hover white-background box-shadow">
                            <h1 className="header black-text align-center small-text margin-left margin-right">{p.title}</h1>
                            <img className="portfolio-image" src={p.imageUrl} alt={p.title}/>
                        </div>
                    </span>
                </a>
                
            );
        })
    ) : (
        <p>Loading Portfolio ...</p>
    );
    return (
        <div className="container-wrapper justify align margin-bottom-- no-background no-border">
            {showPortfolio}
        </div>
    );
}

export default ViewPortfolio