import React from 'react';
import EachSkill from './EachSkill';

const ViewSkills = ({skills, hideSkill}) => {
    const showSkills = skills.length ? (
        skills.map((skill, idx) => {
            return (
                <div key={skill.id} className="skills-container-2 justify align no-background no-border">
                    <h1 className="header black-text small-text bold white-box cursor margin-top--" onClick={() => {hideSkill(idx)}}>{skill.title}</h1>
                    <EachSkill skill={skill.all} show={skill.show}/>
                </div>
            );
        })
    ) : (
        <p>Loading skills ...</p>
    );

    return (
        <div className="skills-container box-shadow">
            <h1 className="header white-text medium-text berlin">SKILLS<span className="orange-text">.</span></h1>
            <div className="scrollable">
                {showSkills}
            </div>
        </div>
    );
}

export default ViewSkills